.App {
  text-align: center;
  padding: 20px;
}

h1 {
  color: #1a73e8; 
}

input {
  padding: 10px;
  font-size: 16px;
  width: 70%;
  border-radius: 30px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #1a73e8; 
  color: white;
  cursor: pointer;
  border: none;
  border-radius: 20px;
}

button:hover {
  background-color: #0a58ca; 
}

.result {
  margin-top: 20px;
  text-align: left;
  display: flex; 
  flex-direction: column;
}

.result-back {
  background-color: lightgrey;
  padding: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 20px;
  width: 40%;
  border-radius: 15px;
}

.result-answer {
  background-color: lightgrey;
  padding: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 20px;
  width: 40%;
  border-radius: 15px;
}

.news-item {
  background-color: lightgrey;
  padding: 10px;
  margin-bottom: 20px;
  margin-right: 20px;
  width: 40%;
  border-radius: 15px;
}

.result h3 {
  color: #1a0dab;
}

.result p {
  color: #5f6368; 
}

.result-item img {
  max-width: 70%; 
  margin-top: 10px; 
}

.no_dot {
  list-style-type: none;
}

